import * as React from 'react';
import { useTheme, Fade, Box , Slide } from '@mui/material';
import Button from '../components/Button';
import Typography from '../components/Typography';
import ProductHeroLayout from './ProductHeroLayout';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';
import config from '../../config.json'

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);



export default function ProductHero() {
  const theme = useTheme();
  const [activeStep, setActiveStep] = React.useState(0);


  const handleStepChange = (step) => {
    setActiveStep(step);
  };

  return (
    <>
      <AutoPlaySwipeableViews
            axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
            index={activeStep}
            onChangeIndex={handleStepChange}
            enableMouseEvents
          >
            {config.slideShow.map((step, index) => (
              
              <ProductHeroLayout
              sxBackground={{
                backgroundImage: `url(${config.slideShow[index].imagem})`,
                backgroundColor: '#7fc7d9', // Average color of the background image.
                backgroundPosition: 'center',
                
              }}
            >
              {/* Increase the network loading priority of the background image. */}
              <img
                style={{ display: 'none' }}
                src={config.slideShow[index].imagem}
                alt="increase priority"
              />
              <Fade  in={true} mountOnEnter unmountOnExit style={{transitionDuration: '1500ms', transitionDelay: '200ms' }}>
                <Box>
                  <Typography color="inherit" align="center" variant="h4" marked="center">
                    {config.slideShow[index].texto1}
                  </Typography>
                </Box>
              </Fade >
              <Slide key={1} direction='up' in={true} mountOnEnter unmountOnExit easing={{enter: "cubic-bezier(0, 1.5, 0.7, 1)", exit: "linear"}}>
                <Box>
                  <Typography
                    color="inherit"
                    align="center"
                    variant="h5"
                    sx={{ mb: '1vh', mt: { xs: '1vh', sm: 10 } }}
                  >
                    {config.slideShow[index].texto2}
                  </Typography>
                </Box>
              </Slide>
              
              <Slide direction='up' in={true} mountOnEnter unmountOnExit >
                <Box>
                  <Button
                    color="secondary"
                    variant="contained"
                    size="large"
                    component="a"
                    href={config.slideShow[index].link}
                    sx={{ minWidth: 200, mt:2}}
                  >
                    {config.slideShow[index].textobotao}
                  </Button>
                </Box>
              </Slide>
            </ProductHeroLayout>
            ))}
          </AutoPlaySwipeableViews>
    </>
  );
}
