import * as React from 'react';
import Box from '@mui/material/Box';
import Link from '@mui/material/Link';
import Popover from '@mui/material/Popover';
import AppBar from '../components/AppBar';
import Toolbar from '../components/Toolbar';
import { Typography,Stack } from '@mui/material';
import './AppAppBar.css'
import config from '../../config.json'
import '../../fonts/perpetua.ttf'

const rightLink = {
  fontSize: 11,
  color: 'common.white',
  ml: 3,
  mb:1 ,
  '&:hover': {
    borderBottom: '1px solid currentColor',
  },
};


function AppAppBar() {
  const [openMenu, setOpenMenu] = React.useState(false)

  const handleCloseMenu = () => {
    setOpenMenu(null);
  };

  return (
    <div>
      <AppBar position="fixed">
        
        <Toolbar sx={{ justifyContent: 'space-between', background:'linear-gradient(90deg, rgb(4, 0, 63) 0%, rgb(4, 1, 46) 14%, rgb(0, 0, 20) 100%)'}}>
        
          <Box href={'/'} sx={{ flex: 1, display: 'flex', justifyContent: 'flex-start', height:'100%', mt:0}}>
            <a href="/">
              <img
                style={{height:'auto', maxHeight:'90%'}}
                src={config.logo}
                alt="increase priority"
              
              />
            </a>
            
            <Box sx={{ flex: 1, display: 'flex', alignItems:'center', borderLeft: '1px solid gray', width:'30vw'}}>
              <Stack sx={{marginLeft: 1}}>
                <Typography className={'fontPerpetua'} sx={{fontSize:'calc(1vw + 0.6vh + 1vmin)'}}>
                {config.appBar.texto1}
                </Typography>
                
                <Typography className={'fontPerpetua'} sx={{fontSize:'calc(0.3vw + 0.1vh + 1vmin)', color:'secondary.main'}}>
                  {config.appBar.texto2}
                </Typography>
              </Stack>
            </Box>
          </Box>
          <Box sx={{ flex: 1, display: 'flex', justifyContent: 'flex-end'}}>
            <Link
              color="inherit"
              variant="h6"
              underline="none"
              sx={rightLink}
            >
              <Box onClick={(e) => setOpenMenu(e.currentTarget)} sx={{cursor: 'pointer'}}>
                {'ÁREAS DE ATUAÇÃO'}
              </Box>
            </Link>
            <Link
              variant="h6"
              underline="none"
              href={config.linkwhats}
              sx={{ ...rightLink, color: 'secondary.main' }}
            >
              {'FALE CONOSCO'}
            </Link>
          </Box>
        </Toolbar>
      </AppBar>

      {/** Pop up das variaveis */}
      <Popover
        open={Boolean(openMenu)}
        anchorEl={openMenu}
        onClose={handleCloseMenu}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
        transformOrigin={{ vertical: 'top', horizontal: 'center' }}
        PaperProps={{
          sx: {
            backgroundColor:'black',
            p: 1,
            mt:0.5
            
          },
        }}
      >
        <Box backgroundColor={'black'} sx={{mr: 3}}>
          <Stack>
            {config.body2.categorias.map((categoria) => (
               <Link
               color="inherit"
               variant="h6"
               underline="none"
               sx={rightLink}
               href={categoria.link}
             >
                  {categoria.title}
              </Link>
            ))}
          </Stack>
        </Box>
      </Popover>
    </div>
    
  );
}

export default AppAppBar;
